<template>
  <div class="login">

    <card header-text="Welcome !">
      <div class="card-body card-block">
        <form method="post" action="/auth/login" name="login" @submit.prevent="post_login" >
          <div class="form-group">
            <div class="input-group">
              <div class="input-group-addon"><i class="fa fa-envelope"></i></div>
              <input type="text" id="email" name="username" class="form-control" v-model="username">
            </div>
          </div>
          <div class="form-group">
            <div class="input-group">
              <div class="input-group-addon"><i class="fa fa-asterisk"></i></div>
              <input type="password" id="password" name="password" placeholder="Password" class="form-control" v-model="pw">
            </div>
          </div>
          <div class="form-actions form-group">
              <button type="submit" class="btn btn-success btn-md">Log In</button>            
          </div>
        </form>
      </div>
    </card>

  </div>
</template>

<script>
export default {
  data(){
    return{
      Status: ""
    }
  },
  methods:{
              post_login(e){  
                        var formData = new FormData();
                        formData.append('username', this.username);
                        formData.append('password',this.pw);
                        axios.post('../login.php',formData) .then(response => {
                        this.Status = response.data
                    if (this.Status == 'OK') {
                        window.location = "https://request.msc.edu.hk";
                        } else {
                            alert(this.Status);
                            this.$router.push('Login');
                        }
                    });                     
              }
       }
 }
</script>

<style lang="scss" scoped>
  .card-title{
    padding-left: 20px;
  }
</style>


